            ÂßìÂêç
          </label>
          <input
            id="name"
            v-model="formData.name"
            type="text"
            required
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÂßìÂêç"
            class="input"
          >
        </div>

        <!-- ÊÄßÂà´ -->
        <div class="form-group">
          <label class="label">
            <span class="label-icon">‚ö§</span>
            ÊÄßÂà´
          </label>
          <div class="radio-group">
            <label class="radio-label">
              <input
                type="radio"
                v-model="formData.gender"
                value="male"
                required
              >
              <span class="radio-text">Áî∑</span>
            </label>
            <label class="radio-label">
              <input
                type="radio"
                v-model="formData.gender"
                value="female"
              >
              <span class="radio-text">Â•≥</span>
            </label>
          </div>
        </div>

        <!-- Âá∫ÁîüÊó•Êúü -->
        <div class="form-group">
          <label class="label">
            <span class="label-icon">üìÖ</span>
            Âá∫ÁîüÊó•Êúü
            <span class="helper-text">ËØ∑ÈÄâÊã©ÊÇ®ÁöÑÂá∫ÁîüÂπ¥ÊúàÊó•</span>
          </label>
          <div class="date-selector">
            <!-- Âπ¥‰ªΩÈÄâÊã© -->
            <select
              v-model="selectedYear"
              class="date-select year-select"
              required
              @change="updateBirthDate"
            >
              <option value="">ËØ∑ÈÄâÊã©Âπ¥‰ªΩ</option>
              <option 
                v-for="year in yearOptions" 
                :key="year.value" 
                :value="year.value"
              >
                {{ year.label }}
              </option>
            </select>

            <!-- Êúà‰ªΩÈÄâÊã© -->
            <select
              v-model="selectedMonth"
              class="date-select month-select"
              required
              @change="updateBirthDate"
            >
              <option value="">Êúà‰ªΩ</option>
              <option v-for="month in 12" :key="month" :value="month">
                {{month}}Êúà
              </option>
            </select>

            <!-- Êó•ÊúüÈÄâÊã© -->
            <select
              v-model="selectedDay"
              class="date-select day-select"
              required
              @change="updateBirthDate"
            >
              <option value="">Êó•Êúü</option>
              <option v-for="day in daysInMonth" :key="day" :value="day">
                {{day}}Êó•
              </option>
            </select>
          </div>
        </div>

        <!-- Âá∫ÁîüÊó∂Ëæ∞ -->
        <div class="form-group">
          <label class="label">
            <span class="label-icon">üïê</span>
            Âá∫ÁîüÊó∂Ëæ∞
            <span class="helper-text">ËØ∑ÈÄâÊã©ÊÇ®ÁöÑÂá∫ÁîüÊó∂Ëæ∞</span>
          </label>
          <div class="time-selector">
            <button
              type="button"
              v-for="time in timeOptions"
              :key="time"
              @click="selectTime(time)"
              :class="{'selected': time === selectedTime}"
              class="time-btn"
            >
              {{ time }}
            </button>
          </div>
        </div>

        <!-- Âá∫ÁîüÂüéÂ∏Ç -->
        <div class="form-group">
          <label for="birthCity" class="label">
            <span class="label-icon">üèôÔ∏è</span>
            Âá∫ÁîüÂüéÂ∏Ç
          </label>
          <input
            id="birthCity"
            v-model="formData.birthCity"
            type="text"
            required
            placeholder="ËØ∑ËæìÂÖ•Âá∫ÁîüÂüéÂ∏Ç"
            class="input"
          >
