          <span class="btn-icon">{{ isLoading ? 'üîÆ' : '‚ú®' }}</span>
          {{ isLoading ? 'ÂëΩÁêÜÂàÜÊûê‰∏≠...' : 'ÂºÄÂßãÂàÜÊûê' }}
        </button>

        <div v-if="error" class="error-message">
          <span class="error-icon">‚ö†Ô∏è</span>
