<script setup>
import { ref, computed } from 'vue'
import dayjs from 'dayjs'
import ResultDisplay from './ResultDisplay.vue'

const formData = ref({
  name: '',
  gender: '',
  birthDate: '',
  birthTime: '',
  birthCity: '',
  maritalStatus: ''
})

const isLoading = ref(false)
const error = ref('')
const showResult = ref(false)
const analysisResult = ref('')
const selectedTime = ref('')

// Êó•ÊúüÈÄâÊã©Áõ∏ÂÖ≥ÁöÑÂìçÂ∫îÂºèÂèòÈáè
const selectedYear = ref('1980')  // ÈªòËÆ§ÈÄâ‰∏≠1980Âπ¥
const selectedMonth = ref('')
const selectedDay = ref('')

// ÁîüÊàêÂπ¥‰ªΩÈÄâÈ°πÔºà‰ª•1980‰∏∫‰∏≠ÂøÉÔºå‰∏ä‰∏ã30Âπ¥ËåÉÂõ¥Ôºâ
const yearOptions = computed(() => {
  const years = []
  const centerYear = 1980
  const range = 30  // ÂáèÂ∞èËåÉÂõ¥‰Ωø1980Âπ¥Êõ¥ÂÆπÊòìÂÆö‰ΩçÂà∞‰∏≠Èó¥
  const startYear = centerYear - range
  const endYear = centerYear + range
  
  for (let year = startYear; year <= endYear; year++) {
    years.push({
      value: year,
      label: `${year}Âπ¥`
    })
  }
  return years
})

// ËÆ°ÁÆóÂΩìÂâçÈÄâÊã©ÁöÑÊúà‰ªΩÂ∫îËØ•ÊúâÂ§öÂ∞ëÂ§©
const daysInMonth = computed(() => {
  if (!selectedYear.value || !selectedMonth.value) return 31
  const date = new Date(selectedYear.value, selectedMonth.value, 0)
  return date.getDate()
})

// Êõ¥Êñ∞Âá∫ÁîüÊó•Êúü
const updateBirthDate = () => {
  if (selectedYear.value && selectedMonth.value && selectedDay.value) {
    const month = String(selectedMonth.value).padStart(2, '0')
    const day = String(selectedDay.value).padStart(2, '0')
    formData.value.birthDate = `${selectedYear.value}-${month}-${day}`
  } else {
    formData.value.birthDate = ''
  }
}

// Êó∂Ëæ∞ÈÄâÈ°π
const timeOptions = [
  'Â≠êÊó∂ (23:00-1:00)',
  '‰∏ëÊó∂ (1:00-3:00)',
  'ÂØÖÊó∂ (3:00-5:00)',
  'ÂçØÊó∂ (5:00-7:00)',
  'Ëæ∞Êó∂ (7:00-9:00)',
  'Â∑≥Êó∂ (9:00-11:00)',
  'ÂçàÊó∂ (11:00-13:00)',
  'Êú™Êó∂ (13:00-15:00)',
  'Áî≥Êó∂ (15:00-17:00)',
  'ÈÖâÊó∂ (17:00-19:00)',
  'ÊàåÊó∂ (19:00-21:00)',
  '‰∫•Êó∂ (21:00-23:00)'
]

// ÈÄâÊã©Êó∂Ëæ∞
const selectTime = (time) => {
  selectedTime.value = time
  // ÊèêÂèñÊó∂Èó¥ËåÉÂõ¥ÁöÑÁ¨¨‰∏Ä‰∏™Â∞èÊó∂‰Ωú‰∏∫Ë°®ÂçïÂÄº
  const hour = time.match(/\d{1,2}(?=:00)/)[0]
  formData.value.birthTime = `${hour.padStart(2, '0')}:00`
}

// Ê†ºÂºèÂåñÊó•ÊúüÊòæÁ§∫
const formatDate = (date) => {
  if (!date) return ''
  return dayjs(date).format('YYYYÂπ¥MMÊúàDDÊó•')
}

// Â§ÑÁêÜË°®ÂçïÊèê‰∫§
const handleSubmit = async () => {
  try {
    if (!selectedTime.value) {
      error.value = 'ËØ∑ÈÄâÊã©Âá∫ÁîüÊó∂Ëæ∞'
      return
    }

    isLoading.value = true
    error.value = ''

    // Ê®°ÊãüÂàÜÊûêËøáÁ®ã
    await new Promise(resolve => setTimeout(resolve, 2000))
    analysisResult.value = `Â∞äÊï¨ÁöÑ${formData.value.name}ÔºåÊÇ®ÁöÑÂÖ´Â≠óÂëΩÁêÜÂàÜÊûêÁªìÊûúÂ∑≤ÁîüÊàê...
Âá∫ÁîüÊó•ÊúüÔºö${formatDate(formData.value.birthDate)}
Âá∫ÁîüÊó∂Ëæ∞Ôºö${selectedTime.value}
Âá∫ÁîüÂüéÂ∏ÇÔºö${formData.value.birthCity}`
    showResult.value = true
  } catch (e) {
    error.value = 'ÂàÜÊûêËøáÁ®ãÂá∫Áé∞ÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï'
  } finally {
    isLoading.value = false
  }
}
</script>

